<template>
<v-container-fluid>
  <v-layout row wrap>
    <v-flex xs-12>
      <v-card hover class="ma-2 pa-2">
        <v-card-title class="display-2">
          <v-flex>
            <p class="text-xs-center">Kendeda Event Calendar</p>
            <p class="text-xs-center display-1">{{calendarMonth + ' ' + calendarYear}}</p>
          </v-flex>
        </v-card-title>
        <v-calendar ref="calendar" type="month" v-model="calendarStart">
          <template v-slot:day="{ date }">
            <template v-for="event in calendarEventsMap[date]">
              <v-menu
                :key="event.title"
                v-model="event.open"
                full-width
                offset-x
              >
                <template v-slot:activator="{ on }">
                  <div
                    v-if="!event.time"
                    v-ripple
                    class="my-event"
                    v-on="on"
                    v-html="event.title"
                  ></div>
                </template>
                <v-card
                  color="grey lighten-4"
                  min-width="350px"
                  flat
                >
                  <v-toolbar
                    color="primary"
                    dark
                  >
                    <v-btn icon>
                      <v-icon>edit</v-icon>
                    </v-btn>
                    <v-toolbar-title v-html="event.title"></v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-btn icon>
                      <v-icon>favorite</v-icon>
                    </v-btn>
                    <v-btn icon>
                      <v-icon>more_vert</v-icon>
                    </v-btn>
                  </v-toolbar>
                  <v-card-title primary-title>
                    <p v-html="event.details"></p>
                  </v-card-title>
                  <v-card-text>
                    <p>Time: </p>
                    <p>Expected Number of People: </p>
                    <!-- <span v-html="event.people"></span -->
                  </v-card-text>
                  <v-card-actions>
                    <v-btn
                      flat
                      color="secondary"
                    >
                      Cancel
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-menu>
            </template>
          </template>
        </v-calendar>
        <v-card-actions>
          <v-flex xs6 class="text-xs-left">
            <v-btn color="primary" @click="$refs.calendar.prev(),getCalendarMonth(),getCalendarYear()"><v-icon>chevron_left</v-icon>Prev</v-btn>
          </v-flex>
          <v-flex xs6 class="text-xs-right">
            <v-btn color="primary" @click="$refs.calendar.next(),getCalendarMonth(),getCalendarYear()">Next<v-icon>chevron_right</v-icon></v-btn>
          </v-flex>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</v-container-fluid>
</template>

<script>
export default{
  data: () => ({
    calendarStart: '',
    calendarMonth: new Date().toLocaleString('en-us', { month: 'long' }),
    calendarYear: new Date().getFullYear(),
    calendarEvents: [
        {
          title: 'Company Event',
          details: 'Recruitment event for internships',
          date: '2019-03-30',
          people: '50',
          open: false
        },
        {
          title: 'Student Club Fundraiser',
          details: 'Student hosted event to raise money for club',
          date: '2019-03-31',
          people: '80',
          open: false
        },
        {
          title: 'Award Ceremony',
          details: 'Department teaching and research award reception',
          date: '2019-04-01',
          people: '100',
          open: false
        },
        {
          title: 'Georgia Tech Foundation Gala',
          details: 'Fundraising event for the GT Foundation',
          date: '2019-03-07',
          people: '150',
          open: false
        },
        {
          title: 'Research Meetings with Sponsors',
          details: 'Organizations who sponsor research receive presentations on projects',
          date: '2019-03-03',
          people: '25',
          open: false
        },
        {
          title: 'Graduate Student Appreciation Event',
          details: 'Reception for grad students and their advisors',
          date: '2019-03-01',
          people: '75',
          open: false
        },
        {
          title: 'Conference',
          details: 'Small conference for undergraduate research',
          date: '2019-03-21',
          people: '250',
          open: false
        },
        {
          title: 'Hackathon',
          details: 'Code like there is no tommorrow',
          date: '2019-03-01',
          people: '100',
          open: false
        }
      ],
  }),
  methods: {
    getCalendarYear(){
      let dateObject = new Date();
      if(this.calendarStart !== ''){
        dateObject = new Date(this.calendarStart.replace("-","/"));
      }
      this.calendarYear = dateObject.getFullYear();
    },
    getCalendarMonth(){
      let dateObject = new Date();
      if(this.calendarStart !== ''){
        dateObject = new Date(this.calendarStart.replace("-","/"));
      }
      this.calendarMonth = dateObject.toLocaleString('en-us',{ month: 'long' });
    },
  },
  computed: {
    calendarEventsMap () {
        const map = {}
        this.calendarEvents.forEach(e => (map[e.date] = map[e.date] || []).push(e))
        return map
    },
  },
}
</script>
